function sliderAccount() {
    var swiper = new Swiper('.account .swiper', {
        slidesPerView: 2.4,
        spaceBetween: 20,
        breakpoints: {
            320: {
                slidesPerView: 1.5,
                spaceBetween: 15,
            },
            401: {
                slidesPerView: 2,
                spaceBetween: 15,
            },
            577: {
                slidesPerView: 2.4,
                spaceBetween: 20,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 20,
            },
            993: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            1201: {
                slidesPerView: 2.4,
                spaceBetween: 20,
            }
        }
    })
}

function sliderQuiz() {
    var swiper = new Swiper('.quiz .swiper', {
        spaceBetween: 20,
        effect: "fade",
        allowTouchMove: false,
        navigation: {
            nextEl: ".quiz .swiper-button-next",
            prevEl: ".quiz .swiper-button-prev",
        },
    })
}

$(document).ready(function() {

    sliderAccount();
    sliderQuiz();

    //Burger-Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__list').toggleClass('header__list--active')
        $('body').toggleClass('fixed-body')
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Select
    $(".custom_select select").niceSelect()

    $('.select2').select2({
        multiple: true,
        placeholder: "",
    });

    //Radio
    $('.form-checkbox').click(function () {
        $(this).toggleClass('form-checkbox--active')
        $('.form-checkbox--radio').removeClass('form-checkbox--active')
    })

    $('.form-checkbox--radio').click(function () {
        $(this).addClass('form-checkbox--active')
        $(this).siblings('.form-checkbox').removeClass('form-checkbox--active')
    })

    //individual-check
    $('.individual-check').click(function () {
        $(this).toggleClass('individual-check--active')
        $('.individual-check__wrap').toggleClass('individual-check__wrap--active')
        $('.individual-check__content2').removeClass('individual-check__content2--active')
    })

    $('.individual-check2').click(function () {
        $(this).toggleClass('individual-check2--active')
        $('.individual-check__content2').toggleClass('individual-check__content2--active')
        $('.individual-check__wrap').removeClass('individual-check__wrap--active')
    })

    $('.form-checkbox').click(function () {
        $('.individual-check__wrap').removeClass('individual-check__wrap--active')
        $('.individual-check__content2').removeClass('individual-check__content2--active')
    })

    //edit
    let edit = document.querySelectorAll('.edit-btn');
    let text = document.querySelectorAll('.edit-text');

    for( let i = 0; i < edit.length; i++ ){
        let editMode = false;

        edit[i].addEventListener('click', function(){
            if( editMode ) {
                this.classList.remove("edit-btn--active")
                text[i].removeAttribute('contentEditable');
            } else {
                this.classList.add("edit-btn--active")
                text[i].setAttribute('contentEditable', true);
                text[i].focus();
            }

            editMode = !editMode;
        });
    }

    //Clone
    let cardConfigTemplate = `
        <div class="individual-check__group">
            <div class="custom_select">
                <select>
                    <option value="АИ-92">АИ-92</option>
                    <option value="АИ-92">АИ-92</option>
                    <option value="АИ-92">АИ-92</option>
                    <option value="АИ-92">АИ-92</option>
                </select>
            </div>
            <div class="form-group">
                <input type="text" placeholder="Процент скидки">
                <div class="individual-btns">
                    <div class="add">+</div>
                    <div class="remove">-</div>
                </div>
            </div>
        </div>
        `

    $(document).on('click', '.add', function(e) {
        $(this).parents('.individual-check__wrap').prepend(cardConfigTemplate)
        $(".custom_select select").niceSelect()
    })

    $(document).on('click', '.remove', function(e) {
        $(this).parents('.individual-check__group').remove()
    })



})